.tint {
  position: relative;
}

.tint:before {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 0;
  top: 0;
  left: 0;
}

.tint-01:before {
  background-color: rgb(0, 0, 0);
}

.tint-01:before {
  opacity: 0.1;
}

.tint-02:before {
  background-color: rgba(0, 0, 0, 0.2);
}

.tint-03:before {
  background-color: rgba(0, 0, 0, 0.3);
}

.tint-04:before {
  background-color: rgba(0, 0, 0, 0.4);
}

.tint-05:before {
  background-color: rgba(0, 0, 0, 0.5);
}

.tint-06:before {
  background-color: rgba(0, 0, 0, 0.6);
}

.tint-07:before {
  background-color: rgba(0, 0, 0, 0.7);
}

.tint-08:before {
  background-color: rgba(0, 0, 0, 0.8);
}

.tint-09:before {
  background-color: rgba(0, 0, 0, 0.9);
}

@media (max-width: 768px) {
  .col {
    flex-grow: 1;
  }
  .wp-block-zenbsblocks-col.col:not(.col-1):not(.col-2):not(.col-3):not(.col-4):not(.col-5):not(.col-6):not(.col-7):not(.col-8):not(.col-9):not(.col-10):not(.col-11):not(.col-12) {
    flex-basis: auto;
    width: 100%;
  }
}